/**
 * FaceTracker - MediaPipe Face Landmarker を使用したフェイストラッキングモジュール
 * Webカメラから顔を検出し、Live2Dパラメータにマッピングする
 */

import {
  FaceLandmarker,
  FilesetResolver,
  FaceLandmarkerResult
} from '@mediapipe/tasks-vision';

/**
 * フェイストラッキングの結果を保持するインターフェース
 */
export interface FaceTrackingData {
  // 顔の角度
  faceAngleX: number; // 左右の角度 (-30 ~ 30)
  faceAngleY: number; // 上下の角度 (-30 ~ 30)
  faceAngleZ: number; // 傾き (-30 ~ 30)

  // 目の開閉 (0 ~ 1)
  eyeOpenL: number;
  eyeOpenR: number;

  // 眉
  browY: number; // 眉の上下 (-1 ~ 1)

  // 口
  mouthOpenY: number; // 口の開き (0 ~ 1)
  mouthForm: number; // 口の形（笑顔など） (-1 ~ 1)

  // 目線
  eyeBallX: number; // 目線の左右 (-1 ~ 1)
  eyeBallY: number; // 目線の上下 (-1 ~ 1)

  // 体の角度
  bodyAngleX: number;
  bodyAngleY: number;

  // 検出状態
  isDetected: boolean;
}

/**
 * フェイストラッカークラス
 */
export class FaceTracker {
  private faceLandmarker: FaceLandmarker | null = null;
  private video: HTMLVideoElement | null = null;
  private isInitialized: boolean = false;
  private lastVideoTime: number = -1;

  // トラッキングデータ
  private _trackingData: FaceTrackingData = {
    faceAngleX: 0,
    faceAngleY: 0,
    faceAngleZ: 0,
    eyeOpenL: 1,
    eyeOpenR: 1,
    browY: 0,
    mouthOpenY: 0,
    mouthForm: 0,
    eyeBallX: 0,
    eyeBallY: 0,
    bodyAngleX: 0,
    bodyAngleY: 0,
    isDetected: false
  };

  // スムージング用の前フレームデータ
  private prevData: FaceTrackingData | null = null;
  private smoothingFactor: number = 0.5; // 0-1: 高いほどスムーズ

  /**
   * トラッキングデータを取得
   */
  public get trackingData(): FaceTrackingData {
    return this._trackingData;
  }

  /**
   * 初期化状態を取得
   */
  public get initialized(): boolean {
    return this.isInitialized;
  }

  /**
   * フェイストラッカーを初期化
   */
  public async initialize(): Promise<boolean> {
    try {
      console.log('[FaceTracker] Initializing MediaPipe Face Landmarker...');

      // MediaPipe Vision タスクの初期化
      const vision = await FilesetResolver.forVisionTasks(
        'https://cdn.jsdelivr.net/npm/@mediapipe/tasks-vision@latest/wasm'
      );

      // Face Landmarker の作成
      this.faceLandmarker = await FaceLandmarker.createFromOptions(vision, {
        baseOptions: {
          modelAssetPath:
            'https://storage.googleapis.com/mediapipe-models/face_landmarker/face_landmarker/float16/1/face_landmarker.task',
          delegate: 'GPU'
        },
        runningMode: 'VIDEO',
        numFaces: 1,
        minFaceDetectionConfidence: 0.5,
        minFacePresenceConfidence: 0.5,
        minTrackingConfidence: 0.5,
        outputFaceBlendshapes: true,
        outputFacialTransformationMatrixes: true
      });

      console.log('[FaceTracker] MediaPipe initialized successfully');

      // Webカメラの初期化
      await this.initializeCamera();

      this.isInitialized = true;
      return true;
    } catch (error) {
      console.error('[FaceTracker] Initialization failed:', error);
      return false;
    }
  }

  /**
   * Webカメラを初期化
   */
  private async initializeCamera(): Promise<void> {
    console.log('[FaceTracker] Initializing webcam...');

    // ビデオ要素を作成
    this.video = document.createElement('video');
    this.video.setAttribute('autoplay', '');
    this.video.setAttribute('playsinline', '');
    this.video.style.position = 'fixed';
    this.video.style.bottom = '10px';
    this.video.style.right = '10px';
    this.video.style.width = '200px';
    this.video.style.height = '150px';
    this.video.style.borderRadius = '8px';
    this.video.style.border = '2px solid #fff';
    this.video.style.zIndex = '1000';
    this.video.style.transform = 'scaleX(-1)'; // 鏡像表示
    document.body.appendChild(this.video);

    // カメラストリームを取得
    const stream = await navigator.mediaDevices.getUserMedia({
      video: {
        width: { ideal: 640 },
        height: { ideal: 480 },
        facingMode: 'user'
      },
      audio: false
    });

    this.video.srcObject = stream;

    // ビデオの準備完了を待つ
    await new Promise<void>(resolve => {
      if (this.video) {
        this.video.onloadedmetadata = () => {
          this.video!.play();
          resolve();
        };
      }
    });

    console.log('[FaceTracker] Webcam initialized successfully');
  }

  /**
   * フレームを更新してトラッキング結果を取得
   */
  public update(): void {
    if (!this.isInitialized || !this.faceLandmarker || !this.video) {
      return;
    }

    // ビデオが再生中でない場合はスキップ
    if (this.video.readyState < 2) {
      return;
    }

    // 同じフレームは処理しない
    const currentTime = this.video.currentTime;
    if (currentTime === this.lastVideoTime) {
      return;
    }
    this.lastVideoTime = currentTime;

    // 顔検出を実行
    const result = this.faceLandmarker.detectForVideo(
      this.video,
      performance.now()
    );

    // 結果を処理
    this.processResult(result);
  }

  /**
   * 検出結果を処理してLive2Dパラメータにマッピング
   */
  private processResult(result: FaceLandmarkerResult): void {
    if (
      !result.faceLandmarks ||
      result.faceLandmarks.length === 0 ||
      !result.faceBlendshapes ||
      result.faceBlendshapes.length === 0
    ) {
      this._trackingData.isDetected = false;
      return;
    }

    this._trackingData.isDetected = true;

    const landmarks = result.faceLandmarks[0];
    const blendshapes = result.faceBlendshapes[0].categories;

    // ブレンドシェイプをマップに変換
    const blendshapeMap = new Map<string, number>();
    for (const shape of blendshapes) {
      blendshapeMap.set(shape.categoryName, shape.score);
    }

    // 顔のランドマークから角度を計算
    this.calculateFaceAngles(landmarks);

    // ブレンドシェイプからパラメータを取得
    // 目の開閉（まばたき）
    const eyeBlinkL = blendshapeMap.get('eyeBlinkLeft') ?? 0;
    const eyeBlinkR = blendshapeMap.get('eyeBlinkRight') ?? 0;
    this._trackingData.eyeOpenL = 1 - eyeBlinkL;
    this._trackingData.eyeOpenR = 1 - eyeBlinkR;

    // 眉
    const browInnerUp = blendshapeMap.get('browInnerUp') ?? 0;
    const browDownL = blendshapeMap.get('browDownLeft') ?? 0;
    const browDownR = blendshapeMap.get('browDownRight') ?? 0;
    this._trackingData.browY = browInnerUp - (browDownL + browDownR) / 2;

    // 口
    const jawOpen = blendshapeMap.get('jawOpen') ?? 0;
    const mouthSmileL = blendshapeMap.get('mouthSmileLeft') ?? 0;
    const mouthSmileR = blendshapeMap.get('mouthSmileRight') ?? 0;
    this._trackingData.mouthOpenY = jawOpen;
    this._trackingData.mouthForm = (mouthSmileL + mouthSmileR) / 2;

    // 目線
    const eyeLookInL = blendshapeMap.get('eyeLookInLeft') ?? 0;
    const eyeLookOutL = blendshapeMap.get('eyeLookOutLeft') ?? 0;
    const eyeLookInR = blendshapeMap.get('eyeLookInRight') ?? 0;
    const eyeLookOutR = blendshapeMap.get('eyeLookOutRight') ?? 0;
    const eyeLookUpL = blendshapeMap.get('eyeLookUpLeft') ?? 0;
    const eyeLookDownL = blendshapeMap.get('eyeLookDownLeft') ?? 0;
    const eyeLookUpR = blendshapeMap.get('eyeLookUpRight') ?? 0;
    const eyeLookDownR = blendshapeMap.get('eyeLookDownRight') ?? 0;

    // 左目と右目の目線を平均化
    const lookRight = (eyeLookOutL + eyeLookInR) / 2;
    const lookLeft = (eyeLookInL + eyeLookOutR) / 2;
    const lookUp = (eyeLookUpL + eyeLookUpR) / 2;
    const lookDown = (eyeLookDownL + eyeLookDownR) / 2;

    this._trackingData.eyeBallX = (lookRight - lookLeft) * 2;
    this._trackingData.eyeBallY = (lookUp - lookDown) * 2;

    // スムージング適用
    this.applySmoothing();
  }

  /**
   * ランドマークから顔の角度を計算
   */
  private calculateFaceAngles(
    landmarks: { x: number; y: number; z: number }[]
  ): void {
    // 鼻先（インデックス1）と顔の中心を使用
    const nose = landmarks[1];
    const leftEye = landmarks[33]; // 左目の端
    const rightEye = landmarks[263]; // 右目の端
    const forehead = landmarks[10]; // 額
    const chin = landmarks[152]; // 顎

    // 左右の角度（X軸）: 鼻の位置から計算
    // 0.5が中心、左右に動くと値が変化
    const angleX = (nose.x - 0.5) * -60;

    // 上下の角度（Y軸）: 鼻の位置から計算
    const angleY = (nose.y - 0.5) * -60;

    // 傾き（Z軸）: 両目の傾きから計算
    const eyeDeltaY = rightEye.y - leftEye.y;
    const eyeDeltaX = rightEye.x - leftEye.x;
    const angleZ = Math.atan2(eyeDeltaY, eyeDeltaX) * (180 / Math.PI);

    this._trackingData.faceAngleX = this.clamp(angleX, -30, 30);
    this._trackingData.faceAngleY = this.clamp(angleY, -30, 30);
    this._trackingData.faceAngleZ = this.clamp(angleZ, -30, 30);

    // 体の角度（顔の動きを控えめに反映）
    this._trackingData.bodyAngleX = angleX * 0.3;
    this._trackingData.bodyAngleY = angleY * 0.3;
  }

  /**
   * スムージングを適用
   */
  private applySmoothing(): void {
    if (this.prevData === null) {
      this.prevData = { ...this._trackingData };
      return;
    }

    const lerp = (a: number, b: number, t: number) => a + (b - a) * t;
    const t = 1 - this.smoothingFactor;

    this._trackingData.faceAngleX = lerp(
      this.prevData.faceAngleX,
      this._trackingData.faceAngleX,
      t
    );
    this._trackingData.faceAngleY = lerp(
      this.prevData.faceAngleY,
      this._trackingData.faceAngleY,
      t
    );
    this._trackingData.faceAngleZ = lerp(
      this.prevData.faceAngleZ,
      this._trackingData.faceAngleZ,
      t
    );
    this._trackingData.eyeOpenL = lerp(
      this.prevData.eyeOpenL,
      this._trackingData.eyeOpenL,
      t
    );
    this._trackingData.eyeOpenR = lerp(
      this.prevData.eyeOpenR,
      this._trackingData.eyeOpenR,
      t
    );
    this._trackingData.mouthOpenY = lerp(
      this.prevData.mouthOpenY,
      this._trackingData.mouthOpenY,
      t
    );
    this._trackingData.eyeBallX = lerp(
      this.prevData.eyeBallX,
      this._trackingData.eyeBallX,
      t
    );
    this._trackingData.eyeBallY = lerp(
      this.prevData.eyeBallY,
      this._trackingData.eyeBallY,
      t
    );

    this.prevData = { ...this._trackingData };
  }

  /**
   * 値を範囲内に制限
   */
  private clamp(value: number, min: number, max: number): number {
    return Math.max(min, Math.min(max, value));
  }

  /**
   * リソースを解放
   */
  public release(): void {
    if (this.video) {
      const stream = this.video.srcObject as MediaStream;
      if (stream) {
        stream.getTracks().forEach(track => track.stop());
      }
      this.video.remove();
      this.video = null;
    }

    if (this.faceLandmarker) {
      this.faceLandmarker.close();
      this.faceLandmarker = null;
    }

    this.isInitialized = false;
    console.log('[FaceTracker] Released');
  }
}

// シングルトンインスタンス
let faceTrackerInstance: FaceTracker | null = null;

/**
 * FaceTrackerのシングルトンインスタンスを取得
 */
export function getFaceTracker(): FaceTracker {
  if (!faceTrackerInstance) {
    faceTrackerInstance = new FaceTracker();
  }
  return faceTrackerInstance;
}
