import { LAppDelegate } from './lappdelegate';
import * as LAppDefine from './lappdefine';
import { getFaceTracker } from './facetracker';

/**
 * ブラウザロード後の処理
 */
window.addEventListener(
  'load',
  async (): Promise<void> => {
    // Initialize WebGL and create the application instance
    if (!LAppDelegate.getInstance().initialize()) {
      return;
    }

    // フェイストラッキングの初期化（有効な場合）
    if (LAppDefine.FaceTrackingEnabled) {
      console.log('[Main] Initializing Face Tracker...');
      const faceTracker = getFaceTracker();
      try {
        const success = await faceTracker.initialize();
        if (success) {
          console.log('[Main] Face Tracker initialized successfully');
        } else {
          console.warn('[Main] Face Tracker initialization failed, using drag mode');
        }
      } catch (error) {
        console.error('[Main] Face Tracker initialization error:', error);
      }
    }

    LAppDelegate.getInstance().run();
  },
  { passive: true }
);

/**
 * 終了時の処理
 */
window.addEventListener(
  'beforeunload',
  (): void => {
    // フェイストラッカーの解放
    if (LAppDefine.FaceTrackingEnabled) {
      getFaceTracker().release();
    }
    LAppDelegate.releaseInstance();
  },
  { passive: true }
);
